<template>
    <div>
        <b-navbar class="navbar fixed-top" toggleable="lg" type="dark" id="navbar">
            <b-navbar-brand class="brand" id="brand" href="#about">
                <img class="brand-logo" id="brand-logo" src="../assets/img/R-Logo-2.png" alt="Brand_Logo">
            </b-navbar-brand>

            <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

            <b-collapse id="nav-collapse" is-nav>
                <b-navbar-nav class="ml-auto">
                    <b-nav-item href="#about"><span class="nav-item">About</span></b-nav-item>
                    <b-nav-item href="#work"><span class="nav-item">Work</span></b-nav-item>
                    <b-nav-item href="#contact"><span class="nav-item">Contact</span></b-nav-item>
                </b-navbar-nav>
            </b-collapse>
        </b-navbar>
    </div>
</template>

<script>
export default {
    name: 'Navbar',
    created () {
        window.addEventListener("scroll", this.scrollFunction);
    },
    methods: {
        scrollFunction() {
            var navbar = document.getElementById("navbar");
            var brandLogo = document.getElementById("brand-logo");
            var brand = document.getElementById("brand");
            if (document.body.scrollTop > 600 || document.documentElement.scrollTop > 600) {
                navbar.style.padding = "30px";
                
                brandLogo.style.height = "40px";
                brandLogo.style.transition = "2s";

                brand.style.left = "5%";
            } else {
                navbar.style.padding = "50px";
                navbar.style.transition = "2s";

                brandLogo.style.height = "80px";

                brand.style.position = "absolute";
                brand.style.left = "48%";
                brand.style.transition = "2s";
            }  
        }
    }
}
</script>

<style scoped>

    @media screen and (max-width: 991.5px) {

        .brand {
            top: 10%;
            left: 3.5% !important;
        }

        .ml-auto {
            margin-top: 60px;
        }
    }

    .navbar {
        font-family: 'Roboto Slab', serif;
        background-color: #191a1c;
        padding: 50px;
    }

    .brand {
        position: absolute;
        left: 48%;
    }

    .brand-logo {
        height: 100px;
        width: auto;
    }

    .navbar-toggler {
        right: 0;
        position: absolute;
        margin-right: 20px;
    }

    .nav-item {
        color:#7bc6dd;
    }

    .nav-item:hover {
        color: #8eb53f;
    }

    .navbar-toggler {
        border: none;
    }

    .show {
        display: block;
    }
</style>