<template>
    <div style="margin-top: 200px;">
        <b-container class="text-center">
            <b-row>
                <b-col md="12">
                    <h2 class="text">Any Thoughts and Suggestions?</h2>
                    <p>Feel free to have a conversation with me.</p>

                    <div class="btn-group mt-4" role="group">
                        <b-button class="btn btn-outline" id="accordion" @click="toggleContactForm"><span class="btn-text">Start a Conversation</span></b-button>
                        <button class="btn btn-solid"><font-awesome-icon class="btn-icon" icon="comment" /></button>
                    </div>
                </b-col>
            </b-row>
        </b-container>
        <hr style="margin-top: 30px" id="target">
        <ContactForm class="contact-form" id="contact-form"/>
    </div>    
</template>

<script>
import ContactForm from '../components/ContactForm'
export default {
    name: 'Contact',
    components: {
        ContactForm
    },
    methods: {
        toggleContactForm() {
            var target = document.getElementById("target");
            var contactForm = target.nextElementSibling;

            if (contactForm.style.maxHeight) {
            contactForm.style.maxHeight = null;
            } else {
            contactForm.style.maxHeight = contactForm.scrollHeight + "px";
            }
    }
    }
    
}
</script>

<style scoped>

    .text {
        font-family: 'Nunito', sans-serif;
        font-weight: 700;
    }

    .btn-outline {
        border: 2px solid #8eb53f;
        border-radius: 2px;
        background-color: white;
        padding: 50px;
        transition: 0.5s;
        color: #8eb53f;
    }

    .btn-outline:hover {
        background-color: #8eb53f;
        color: white;
    }

    .btn-solid {
        background-color: #8eb53f;
        color: white;
        pointer-events: none;
    }

    .btn-text {
        font-size: 20px;
    }

    .btn-icon {
        font-size: 50px;
    }

    .btn-primary {
        content: "Send" !important;
    }

    .contact-form {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.2s ease-out;
    }
</style>