<template>
    <div>
        <b-container>
            <b-form>
                <b-row>
                    <b-col sm="12" md="6">
                        <b-form-group id="email" label="Email: *" label-for="input-email">
                            <b-form-input
                            id="input-email"
                            v-model="form.email"
                            type="email"
                            required
                            placeholder="Enter email"
                            ></b-form-input>
                        </b-form-group>

                        <b-form-group id="input-group-2" label="Your Name:" label-for="input-2">
                            <b-form-input
                            id="input-2"
                            v-model="form.name"
                            placeholder="Enter name"
                            ></b-form-input>
                        </b-form-group>

                        <b-label>Message: *</b-label>
                        <b-form-textarea class="mt-2" id="textarea" v-model="message" placeholder="What's on your mind ?" 
                        rows="3" max-rows="6" required></b-form-textarea>

                        <b-button @click="onSubmit" class="btn-outline mt-3">Send</b-button>
                    </b-col>
                    <b-col sm="12" md="6" class="informations-card text-center p-5">
                        <p>All informations you'll provide here are safe and secure.
                            Here are some more links to contact me with feel free to say hi to me.</p>
                    </b-col>
            </b-row>
            </b-form>    
        </b-container>    
    </div>    
</template>

<script>
export default {
    name: 'ContactForm',
    data() {
      return {
        form: {
          email: '',
          name: '',
          message: ''
        }
      }
    },
    methods: {
      onSubmit(e) {
        e.preventDefault()
        this.onReset(e);
      },
      onReset(e) {
        e.preventDefault()
        this.form.email = ''
        this.form.name = ''
      }
    }
    
}
</script>

<style scoped>
    .btn-outline {
        background-color: white;
        border: 2px solid #8eb53f;
        height: 50px;
        width: 100px;
        color: #8eb53f;
        transition: 0.3s;
    }

    .btn-outline:hover {
        background-color: #8eb53f;
        color: white;
        transition: 0.3s;
    }

    .informations-card {
        background-color: #f7f7f7;
        color: #191a1c;
        border-radius: 5px;
        box-shadow: 0 0 2px gray;
    }
</style>